import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable } from 'rxjs';
import * as moment from 'moment';

@Injectable({
	providedIn: 'root'
})
export class ChatCallService {

	socket

	constructor() {
		this.socket = io('http://localhost:3000')
	}


	connect(name, room) {
		return Observable.create(observe => {


			this.socket.on('connect', () => {
				console.log('connected to server')
				let data = {
					name: name,
					room: room
				}
				this.socket.emit('join', data, (err) => {
					if (err) {
						alert(err)
						window.location.href = '/login'
					} else {
						console.log('no error')
					}
				})
			})

		})
	}

	messageComes() {
		return Observable.create(observe => {
			this.socket.on('newMessage', (msg) => {
				let formateTime = moment(msg.createdAt).format('LT')
				console.log("message come form server-->",msg)
				let data={
					text:msg.text,
					time:formateTime,
					from:msg.from
				}
				observe.next(data)
			})
		})
	}

	messageEmit(data){
		this.socket.emit('createMessage',data);
	}

	updateUserList(){
		return Observable.create(observe=>{
			this.socket.on('updateUserList',data=>{
				observe.next(data)
			})
		})
	}
}